<template>
  <div>
      <img-loader :images='itempic' @on-del='handleDelPic' @on-add='handleAddPic' @on-change='handleChange'></img-loader>
  </div>
</template>
<script>
import multiLoader from '../common/MultiImgUploader.vue'
export default {
  data() {
    return {
      itempic:[{id:1,url:'https://o5wwk8baw.qnssl.com/a42bdcc1178e62b4694c830f028db5c0/avatar'},{url:'https://o5wwk8baw.qnssl.com/bc7521e033abdd1e92222d733590f104/avatar',id:2},{url:'http://p27vbqiwg.bkt.clouddn.com/LdBDPLuR49814549.jpg',id:3}],
    }
  },
  methods: {
    handleDelPic(picid) {
      console.log('删除一张图片',picid)
    },
    handleAddPic(url) {
      console.log('增加一张图片',url)
    },
    handleChange(data) {
      
    },
    
    confirm(text, title) {
      let that = this
      return new Promise((resolve, reject) => {
        that.$Modal.confirm({
          title: title ? title : '确认',
          content: text,
          onOk: () => {
            return resolve(true)
          },
          onCancel: () => {
            return reject(false)
          }
        })
      })
    },
  },
  mounted() {
    
  },
  components:{
    'img-loader':multiLoader
  }
}

</script>
<style scoped>


</style>




